* {
  box-sizing: border-box;
}

p, h2, h3 {
  margin: 0;
  padding-top: 10px;
  line-height: 1.3;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 30px 0;
}

.container-buttons {
  background-color: #dce9ef;
  padding: 50px 0;
}

.menu-list {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.menu-link {
  color: #212121;
  cursor: pointer;
  background-color: #a3cfeb;
  border-radius: 4px;
  align-items: center;
  padding: 10px 20px;
}

.menu-link--active {
  color: #fafafa;
  background-color: #143fe8;
}

.container-palete {
  grid-template-columns: repeat(3, 3fr);
  grid-auto-rows: 200px;
  gap: 20px;
  margin: 0 auto;
  padding: 50px 0;
  display: grid;
}

.palete-js {
  padding: 20px 0;
}

.color-card {
  cursor: pointer;
  width: 200px;
  height: auto;
  background-color: #fafafa;
  border: none;
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 5px;
  transition: transform .25s;
  display: flex;
}

.color-swatch--is-active {
  transform: scale(1.05);
}

.color-swatch {
  width: 100%;
  height: 200px;
  border-radius: 10px;
}

.color-meta {
  padding-top: 10px;
  font-weight: 600;
}

.container-search {
  background-color: #d1f1de;
  flex-direction: column;
  justify-content: center;
  gap: 100px;
  padding: 50px 20px;
  display: flex;
}

.input-search {
  height: 30px;
  border-radius: 10px;
  font-size: 14px;
}

.input-text {
  padding-bottom: 20px;
  font-size: 16px;
  font-weight: 500;
}

.input-rigth-search {
  height: 30px;
  border-radius: 10px;
  outline: none;
}

.valid {
  border: 2px solid green;
}

.invalid {
  border: 2px solid red;
}

.container-filter {
  background-color: #b6dded;
  padding: 50px 20px;
}

.form {
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  display: flex;
}

label {
  text-transform: uppercase;
  gap: 5px;
  display: grid;
}

#filter {
  width: 400px;
  height: 40px;
  padding-left: 10px;
}

.list-js {
  gap: 5px;
  padding: 0;
  display: grid;
}

@media screen and (min-width: 320px) {
  .container-cat {
    background-color: #dbedb6;
    padding: 50px 20px;
  }

  .image-cat {
    max-width: 600px;
    min-height: 400px;
    display: block;
  }

  .image-list {
    flex-direction: column;
    align-items: center;
    gap: 5px;
    display: flex;
  }
}

.list-img-dog {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.img-dog {
  max-width: 800px;
  width: 100%;
  opacity: 0;
  object-fit: cover;
  transition: all .5s linear;
  transform: translateX(-50%);
}

.img-dog.appear {
  opacity: 1;
  transform: translateX(0%);
}

.wrap-container-game {
  background-color: #b4e0d1;
  padding: 50px 0;
}

.container-game {
  width: 152px;
  border: 1px solid #1f4438;
  margin: auto;
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  color: #1f4438;
  cursor: pointer;
  vertical-align: middle;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid #1f4438;
  font-size: 25px;
  font-weight: 700;
  line-height: 50px;
  transition: all cubic-bezier(.19, 1, .22, 1);
  display: inline-block;
}

.cats-list {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-items: center;
  display: grid;
}

.modal {
  background-color: #d8dbde;
  padding: 20px;
}

.cat-img {
  transition: transform .3s, box-shadow .3s;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, .376);
}

.text-error {
  color: red;
}

.cat-img:hover {
  transform: scale(1.05);
}

.modal-cat-name {
  font-weight: 500;
}

.modal-price {
  font-size: 22px;
  font-weight: 700;
}

@media (max-width: 768px) {
  .cats-list {
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

@media (max-width: 480px) {
  .cats-list {
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

